.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.header__container {
  position: relative;
}
@media (max-width: 767px) {
  /* стилизация выпадающего меню*/
  .navigation__toggle {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 28px;
    height: 24px;
    background-color: white;
    cursor: pointer;
    border: none;
  }
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 0;
    width: 28px;
    height: 4px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .navigation--closed .navigation__toggle:active::before {
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
  }
  .navigation--opened .navigation__toggle {
    top: 20px;
    right: 20px;
  }
  .navigation--opened .navigation__toggle::before,
  .navigation--opened .navigation__toggle::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 30px;
    height: 4px;
    background-color: #000000;
  }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .navigation--opened .navigation__toggle::after {
    transform: rotate(-45deg);
  }
  .navigation--opened .navigation__toggle:active::before,
  .navigation--opened .navigation__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
  /* стилизация ссылок*/
  .navigation__wrapper {
    width: 100%;
  }
  .navigation__list {
    width: 100%;
    margin: 0;
  }
  .navigation__item {
    width: 100%;
    min-width: 280px;
    min-height: 65px;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navigation__item-link {
    color: #111111;
    font-size: 20px;
    font-weight: 500;
    line-height: 20px;
    text-transform: uppercase;
  }
  /*скрытые состояния меню*/
  .navigation--closed .navigation__item {
    display: none;
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
  .navigation--nojs .navigation__item {
    display: flex;
  }
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 1;
    flex-wrap: wrap;
    align-items: flex-end;
  }
  .navigation__toggle {
    display: none;
  }
  .navigation__list {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    margin-bottom: 7px;
  }
  .navigation__item {
    margin: 0;
    margin-left: 10px;
  }
  .navigation__item-link {
    color: #222222;
    font-size: 18px;
    font-weight: 400;
    line-height: 24.86px;
    text-transform: uppercase;
    letter-spacing: 0.6px;
  }
  .navigation__item-link:hover {
    opacity: 0.6;
  }
  .navigation__item-link:active {
    opacity: 0.3;
  }
  .navigation__item--active {
    padding-bottom: 10px;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1300px) {
  .header__container--image .navigation__item-link {
    color: #ffffff;
    font-size: 20px;
    font-weight: 400;
    line-height: 24.86px;
    text-transform: uppercase;
  }
  .header__container--image .navigation__item-link:hover {
    opacity: 0.6;
  }
  .header__container--image .navigation__item-link:active {
    opacity: 0.3;
  }
  .navigation {
    margin-top: 32px;
  }
  .navigation__item-link {
    color: #000000;
  }
  .navigation__item-link:hover {
    opacity: 0.6;
  }
  .navigation__item-link:active {
    opacity: 0.3;
  }
  .header__container--image .navigation__item--active {
    border-bottom: 2px solid white;
  }
}
