  line-height: 18px;
  text-transform: uppercase;
  text-align: left;
  margin-top: 0;
}
.form__container {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin-top: 30px;
}
/*общие стили*/
.form__label {
  display: flex;
  flex-direction: row;
  justify-content: start;
  position: relative;
  align-items: center;
}
.form__label-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.form__input {
  flex-grow: 1;
  padding: 5px 45px 5px 20px;
  border: 2px solid rgba(205, 205, 205, 0.5);
}
.form__input:hover {
  border: 2px solid #cdcdcd;
}
.form__input:focus {
  border: 2px solid #444444;
}
.form__input:invalid {
  border: 2px solid #ff8282;
}
.form__input:focus ~ svg {
  fill: #68b738;
}
.form__input:invalid ~ svg {
  fill: #ff8282;
}
.form__input-icon {
  position: absolute;
  top: 34%;
  right: 20px;
  width: 17px;
  height: 17px;
}
/*about*/
.form__about {
  display: flex;
  flex-direction: column;
}
.form__container--about,
.form__container--radio {
  margin-top: 13px;
  border-bottom: 1px solid rgba(205, 205, 205, 0.5);
}
.form__label--info {
  margin: 14px 0;
}
.form__input--info {
  padding: 5px 17px 5px 9px;
  margin-left: 8px;
}
.form__input--info,
.form__input--data {
  height: 31px;
}
/*Radio*/
.form__input--radio {
  flex-grow: 0;
  display: none;
}
.form__label--radio {
  position: relative;
  padding-left: 51px;
  margin: 23px 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.form__label--radio:hover::before {
  border: 1px solid #bababa;
}
.form__label--radio:focus {
  opacity: 0.3;
}
.form__label--radio::before {
  content: "";
  position: absolute;
  left: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50px;
  width: 30px;
  height: 30px;
}
.form__label--radio::after {
  content: " ";
  position: absolute;
  top: 12px;
  left: 0;
  background: #68b738;
  margin-top: 0;
  transform: translate(10px, -9px);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  display: none;
}
.form__input--radio:checked + .form__label--radio::after {
  display: block;
}
/*Contackt*/
.form__contact-wrapper {
  margin-top: 35px;
}
.form__title--data {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0;
  margin-top: 0;
}
.form__label--data {
  margin: 0;
  margin-top: 15px;
}
.form__input--data {
  margin-left: 5px;
}
/*coments*/
.form__input--coments {
  padding: 20px 5px 0 5px;
  margin-top: 10px;
}
.form__title--coments {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0;
  margin-top: 0;
}
/*CHEKBOX*/
.form__container--checkbox {
  margin-top: 30px;
}
.form__title--additionaly {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0;
  margin-top: 0;
}
.form__checkbox-wrapper {
  margin-top: 34px;
}
.form__input--checkbox {
  flex-grow: 0;
  display: none;
}
.form__label--checkbox {
  position: relative;
  padding-left: 50px;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.form__label--checkbox:hover::before {
  border: 1px solid #bababa;
}
.form__label--checkbox:focus {
  opacity: 0.3;
}
.form__label--checkbox::before {
  content: " ";
  position: absolute;
  left: 0;
  border: 1px solid #cdcdcd;
  width: 30px;
  height: 30px;
}
.form__label--checkbox::after {
  content: "";
  left: 10px;
  display: none;
  background: url("../img/checkbpxarrow.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 21px;
  height: 16px;
  position: absolute;
}
.form__input--checkbox:checked + .form__label--checkbox::after {
  display: block;
}
/*send*/
.form__container-send {
  display: flex;
  flex-direction: column;
  margin-top: 24px;
}
.form__send-button {
  color: #ffffff;
  background-color: #68b738;
  height: 40px;
}
.form__text-discription {
  text-align: center;
}
